<template>
  <div class="straightBasket">
    <v-container fluid>
      <!-- 語系選單(需隱藏) -->
      <SelectSelectLanguage v-show="false"/>

      <v-layout
        row
        wrap>
        <v-flex
          xs8
          tag="h3"
          class="mt-0">
          {{ $ln(gname) }}-{{ $ln('注單明細') }}
        </v-flex>
        <v-flex
          xs4
          class="text-xs-right">
          <v-btn
            small
            class="grey"
            @click="closeWin()">
            {{ $ln('關閉視窗') }}
          </v-btn>
        </v-flex>
      </v-layout>

      <v-tabs
        v-model="tabItems.active"
        slider-color="yellow">
        <v-tab
          v-for="(item, i) in tabItems.id"
          :key="i"
          ripple>
          {{ $ln(item) }}
        </v-tab>
        <v-tab-item
          v-for="(item, i) in tabItems.id"
          :key="i">
          <v-layout
            row
            wrap>
            <SportBallStatus
              classType="classCheckView"
              :dataSelect.sync="selectChangeItems.selected"
              :items="selectChangeItems.options"
              title="玩法"
            />

            <SportBallStatus
              classType="list"
              :dataSelect.sync="selectListItems.selected"
              :items="selectListItems.options"
              title="注單顯示"
              class="selectHide"
            />

            <!-- 每秒更新 -->
            <CommonCounter
              :reciprocalItems.sync="reciprocalItems"
              :updateTableList="updateTableList"
            />

            <v-flex
              xs12
              md3
              class="px-1">
              <v-layout
                row
                wrap>
                <SportBallStatus
                  classType="classType"
                  :dataSelect.sync="selectRowsPerPageItems.selected"
                  :items="selectRowsPerPageItems.options"
                  title="每頁顯示"
                />
                <v-flex
                  xs4
                  md2
                  class="grey--text text--darken-2 d-flex align-center">
                  {{ $ln(`共 %s 筆`, pagination.totalItems) }}
                </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>

          <div
            v-if="loadingStatus"
            class="text-xs-center">
            <LoadingPage/>
          </div>

          <div v-else class="pt-1">

            <SportDetailTable
              :header.sync="headers"
              :contents.sync="BillList"
              :gameTime="gameTime"
            />

            <div v-if="pages > 0" class="text-xs-center pt-2">
              <v-pagination
                v-model="pagination.currentPage"
                :length="pages"
                :total-visible="pagination.totalVisible"
                color="deep-orange darken-1"
                @input="loadNextPage"/>
            </div>
          </div>
        </v-tab-item>
      </v-tabs>
    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import $ from 'jquery'
import LoadingPage from '@/components/material/LoadingPage'
import CheckView from '@/mixins/sport/CheckView.vue'

export default {
  name: 'HorseCheckView',
  mixins:[CheckView],
  components: {
    LoadingPage
  },
  data () {
    return {
      gname: '賽馬＆賽狗',
      // 無資料顯示
      showNoData: '目前無任何資料',
      // 注單明細 盤口統計
      tabItems: {
        id: ['注單明細'],
        active: ''
      },
      // 注單選擇
      selectListItems: {
        selected: 'Y',
        options: [
          { text: '只顯示有效注單', value: 'Y' },
          { text: '只顯示已刪除注單', value: 'D' }
        ]
      },
      // 每頁顯示筆數
      selectRowsPerPageItems: {
        selected: 15,
        options: [
          { text: '15筆', value: 15 },
          { text: '30筆', value: 30 },
          { text: '60筆', value: 60 }
        ]
      },
      // 玩法
      selectChangeItems: {
        selected: '',
        options: [
          { text: '全部', value: '2,4' },
          { text: '大小', value: '2' },
          { text: '單雙', value: '4' }
        ]
      },
      // 列表 header
      headers: [
        { text: '帳務日期', align: 'center', sortable: false },
        { text: '投注時間/開賽時間', align: 'center', sortable: false },
        { text: '單號', align: 'center', sortable: false },
        { text: '會員', align: 'center', sortable: false },
        { text: '類別', align: 'center', sortable: false },
        { text: '型態', align: 'center', sortable: false },
        { text: '裝置', align: 'center', sortable: false },
        { text: '內容', align: 'center', sortable: false },
        { text: '金額', align: 'center', sortable: false },
        { text: '輸贏結果', align: 'center', sortable: false },
        { text: '返水', value: 'refund', align: 'center', sortable: false },
        { text: '會員結果', align: 'center', sortable: false }
      ],
      // 表格內容
      BillList: [],
      // 從單式傳過來的開賽時間
      gameTime: '',
      // 頁面載入狀態
      loadingStatus: false,
      // 傳入參數
      gamelist: {
        act: 'normal',
        game_serial_number: '',
        bet_team: '',
        posting: '',
        type: '',
        game_type: '',
        status: '',
        ball: 12,
        page: 1,
        count: ''
      }
    }
  },
  computed: {
    ...mapGetters({
      getBillListState: 'sports/getBillListState'
      // 載入個人帳號
      // getProfileState: 'account-management/getProfileState'
    })
  },
  watch: {
    getBillListState (payload) {
      this.BillList = payload.result.data_list
      // 若 API 有當前頁數回傳資料，可設定如下
      // this.pagination.currentPage = payload.result.current_page
      this.pagination.totalItems = payload.result.total_count
      this.BillList.contents = payload.result.list
    },
    'selectListItems.selected' (value,from) {
      this.gamelist.status = value
      if(from !=='') {
        this.loadBillList()
      }
    },
    'selectRowsPerPageItems.selected' (value) {
      this.pagination.rowsPerPage = value
      this.loadBillList()
    },
    'selectChangeItems.selected' (value) {
      console.log(value)
      this.gamelist.type = value
      this.loadBillList()
    },
    getProfileState (payload) {
      this.gamelist.account = payload.result.l_no
    }
  },
  mounted () {
    // 從單式傳過來的開賽時間
    this.gameTime = this.$route.query.item
    this.gamelist.type = this.$route.query.type
    this.selectChangeItems.selected = this.$route.query.type
  },
  methods: {
    ...mapActions({
      getBillList: 'api/getBillList'
    }),
    // 載入公告
    loadBillList (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        act: this.gamelist.act,
        game_serial_number: this.$route.query.gameSN,
        // game_serial_number: this.gamelist.game_serial_number,
        game_type: this.$route.query.game_type,
        type: this.gamelist.type,
        bet_team: this.$route.query.bet_team,
        posting: this.$route.query.Posting,
        status: this.selectListItems.selected,
        ball: this.gamelist.ball,
        page: page !== undefined ? page : this.pagination.currentPage,
        count: this.pagination.rowsPerPage = this.gamelist.count ? this.gamelist.count : this.selectRowsPerPageItems.selected
      }
      this.getBillList(params)
        .then(res => {
          if (res.status === 200) {
            // 載入狀態(結束)
            this.loadingStatus = false
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    closeWin () {
      window.close()
    }
  }
}
</script>
