
<script>
import { mapGetters, mapActions } from 'vuex'
import $ from 'jquery'
import LoadingPage from '@/components/material/LoadingPage'

export default {
  name: 'CheckView',
  components: {
    LoadingPage
  },
  data () {
    return {
      // 無資料顯示 @mixins@
      showNoData: '目前無任何資料',
      // 注單明細 盤口統計 @mixins@
      tabItems: {},
      // 秒數倒數更新
      reciprocalItems: {
        selected: '',
        options: [
          { text: '每10秒更新', value: 10 },
          { text: '每30秒更新', value: 30 },
          { text: '不更新', value: '' }
        ],
        // 倒數 timer
        updateListTimer: null,
        // 當前秒數
        currentSpeed: '',
        // 確定更新秒數
        confirmSpeed: ''
      },
      // 注單選擇 @mixins@
      selectListItems: {},
      // 每頁顯示筆數 @mixins@
      selectRowsPerPageItems: {},
      // 玩法（一般玩法）@mixins@
      selectChangeItems: {},
      // 玩法（多種玩法）@mixins@
      selectChangeItems2: {},
      // 列表 header @mixins@
      headers: [],
      // 表格內容 @mixins@
      BillList: [],
      // 從單式傳過來的開賽時間 @mixins@
      gameTime: '',
      // 頁數
      pagination: {
        currentPage: 1, // 當前頁數
        rowsPerPage: 15, // 每頁筆數
        totalItems: 0, // 總筆數
        totalVisible: 10 // 顯示最大可見頁數按鈕，其他會用 ... 顯示
      },
      // 頁面載入狀態 @mixins@
      loadingStatus: false,
      // 傳入參數 @mixins@
      gamelist: {}
    }
  },
  computed: {
    // @mixins@
    pages () {
      if (this.pagination.rowsPerPage == null || this.pagination.totalItems == null) return 0
      return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    }
  },
  watch: {
    // @mixins@
    getBillListState (payload) {},
    'pagination.rowsPerPage' (rowsPerPage) {
      // 回第一頁
      this.pagination.currentPage = 1
    },
    // 每秒更新切換
    'reciprocalItems.selected' (value) {
      this.updateTableList()
    },
    'reciprocalItems.currentSpeed' (value) {
      // 倒數為0秒時刷新列表
      if (value === 0) {
        this.loadBillList()
      }
    },
    // @mixins@
    'selectRowsPerPageItems.selected' (value) {},
    // @mixins@
    'selectChangeItems.selected' (value) {},
    // @mixins@
    'selectChangeItems2.selected' (value) {},
    // @mixins@
    'selectListItems.selected' (value) {},
    // @mixins@
    getProfileState (payload) {}
  },
  mounted () {
    // @mixins@

    setTimeout(() => {
      this.unsetVWindowHeight()
    }, 500)
  },
  destroyed () {
    // 清除 timer
    clearInterval(this.reciprocalItems.updateListTimer)
  },
  methods: {
    // 載入明細 @mixins@
    loadBillList (page, rowsPerPage) {},
    // 載入下一頁
    loadNextPage (page) {
      // 若 API 有當前頁數回傳資料，可註解以下
      this.pagination.currentPage = page
      this.loadBillList()
    },
    /**
     * 秒數倒數
     * @params  [speed]  倒數秒數
     **/
    startCountdown (speed) {
      this.reciprocalItems.currentSpeed = speed
      this.reciprocalItems.updateListTimer = setInterval(() => {
        this.reciprocalItems.currentSpeed--
        // 秒數為負數，則重新倒數
        if (this.reciprocalItems.currentSpeed < 0) {
          // 清除 timer
          clearInterval(this.reciprocalItems.updateListTimer)
          // 重新倒數秒數
          this.startCountdown(this.reciprocalItems.confirmSpeed)
        }
      }, 1000)
    },
    // 啟動秒數倒數刷新表格資料
    updateTableList () {
      this.loadBillList()

      // 確定更新秒數
      this.reciprocalItems.confirmSpeed = this.reciprocalItems.selected

      // 清除 timer
      clearInterval(this.reciprocalItems.updateListTimer)

      // 秒數為負數，則不更新倒數功能
      if (this.reciprocalItems.selected < 0) {
        // 當前秒數設置空值
        this.reciprocalItems.currentSpeed = ''
        return
      }
      // 倒數秒數
      this.startCountdown(this.reciprocalItems.confirmSpeed)
    },
    // @mixins@
    closeWin () {},
    // 移除 v-tab 套件於 v-window__container--is-active 產生的 height: 0px;
    unsetVWindowHeight () {
      $('.v-window > .v-window__container--is-active').prop('style', 'height: unset;')
    }
  }
}
</script>
